<template>
    <div class="foodWarp">
        <header>
            <router-link to="/msite">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <section>
                <span>{{fenName}}</span>
            </section>
        </header>
        <div class="xia_la">
              <div class="sort_item" >
                  <div class="sort_item_container" @click="checkShow">
                      <div class="sort_item_border">
                      <span v-if="isShow">
                          {{fenName}}
                      </span>
                          <i class="el-icon-caret-bottom" v-if="isShow"></i>
                          <span v-if="zuo_isShow" style="color: #3D97E9">分类</span>
                          <i class="el-icon-caret-top" v-if="zuo_isShow" style="color:#3D97E9 "></i>
                      </div>
                  </div>
                  <section class="fen" v-if="zuoisShow">
                      <section class="fen_zuo">
                          <ul>
                              <!--<li class="fen_zuo_li" v-for="(item ,index) in fenArr" :key="item.id">-->
                                  <!--<section>-->
                                      <!--<span class="fen_span1">{{item.name}}</span>-->
                                  <!--</section>-->
                                  <!--<section>-->
                                      <!--<span class="fen_span2">{{item.count}}</span>-->
                                  <!--</section>-->
                              <!--</li>-->
                              <li class="fen_zuo_li" v-for="(item, index) in fenArr" :key="item.id" @click="dianfenBtn(item.sub_categories)">
                                  <section>
                                      <img :src="imgArr[index]" alt="" class="fen_zuo_img">
                                      <span class="fen_span1">{{item.name}}</span>
                                  </section>
                                  <section class="fen_zuo_section">
                                      <span class="fen_span2">{{item.count}}</span>
                                      <i class="el-icon-arrow-right" style="color: rgb(187, 187, 187);font-size: .3rem"></i>
                                  </section>
                              </li>
                          </ul>
                      </section>
                      <section class="fen_you">
                          <ul>
                              <li class="fen_you_li" v-for="item1 in rightfenArr" :key="item1.id" @click="fenlieclass(item1)">
                                  <span>{{item1.name}}</span>
                                  <span>{{item1.count}}</span>
                              </li>
                          </ul>
                      </section>
                  </section>
              </div>
              <div class="sort_item">
                  <div class="sort_item_container" @click="paiBtn">
                      <div class="sort_item_border">
                      <span v-if="isShow1">
                          排序
                      </span>
                          <i class="el-icon-caret-bottom" v-if="isShow1"></i>
                          <span v-if="isPai" style="color:#3D97E9 ">
                          排序
                      </span>
                          <i class="el-icon-caret-top" v-if="isPai" style="color: #3D97E9"></i>
                      </div>
                  </div>
                   <section class="sort_detail_type" v-if="isPai1">
                       <ul class="sort_list_container">
                           <li class="sort_list_li" @click="chooseSort">
                               <i class="el-icon-sort"></i>
                               <p class="sort_select">
                                   <span>智能排序</span>
                               </p>
                               <!--<i class="el-icon-check"></i>-->
                           </li>
                           <li class="sort_list_li" @click="chooseSort1">
                               <i class="el-icon-location-outline" style="width: .7rem;height: .7rem; margin: 0 .3rem 0 .8rem; color: rgb(42, 155, 211)"></i>
                               <p class="sort_select">
                                   <span>距离最近</span>
                               </p>
                               <!--<i class="el-icon-check"></i>-->
                           </li>
                           <li class="sort_list_li" @click="chooseSort2">
                               <i class="el-icon-location-outline"  style="width: .7rem;height: .7rem; margin: 0 .3rem 0 .8rem; color:rgb(240, 115, 115) "></i>
                               <p class="sort_select">
                                   <span>销量最高</span>
                               </p>
                               <!--<i class="el-icon-check"></i>-->
                           </li>
                           <li class="sort_list_li" @click="chooseSort3">
                               <i class="el-icon-time"  style="width: .7rem;height: .7rem; margin: 0 .3rem 0 .8rem; color:rgb(230, 182, 26);"></i>
                               <p class="sort_select">
                                   <span>起送价最低</span>
                               </p>
                               <!--<i class="el-icon-check"></i>-->
                           </li>
                           <li class="sort_list_li" @click="chooseSort4">
                               <i class="el-icon-time"  style="width: .7rem;height: .7rem; margin: 0 .3rem 0 .8rem; color:rgb(42, 155, 211) "></i>
                               <p class="sort_select">
                                   <span>配送速度最快</span>
                               </p>
                               <!--<i class="el-icon-check"></i>-->
                           </li>
                           <li class="sort_list_li" @click="chooseSort5">
                               <i class="el-icon-star-off"  style="width: .7rem;height: .7rem; margin: 0 .3rem 0 .8rem; color:rgb(230, 182, 26) "></i>
                               <p class="sort_select">
                                   <span>评分最高</span>
                               </p>
                               <!--<i class="el-icon-check"></i>-->
                           </li>
                       </ul>
                   </section>
              </div>
              <div class="sort_item">
                  <div class="sort_item_container" @click="saiBtn">
                      <div class="sort_item_border">
                      <span v-if="isShow2">
                          赛选
                      </span>
                          <i class="el-icon-caret-bottom" v-if="isShow2"></i>
                       <span v-if="isShowup" style="color:#3D97E9 ">
                          赛选
                      </span>
                          <i class="el-icon-caret-top" v-if="isShowup" style="color:#3D97E9 "></i>
                      </div>
                  </div>
                  <section class="sai" v-if="isShow3">
                      <section style="width: 100%">
                          <div class="sai_header">配送方式</div>
                          <div class="sai_div2">
                              <div class="sai_div3" @click="saixuan(count)">
                                  <!--对号-->
                                  <i class="el-icon-check" v-if="isSai"></i>
                                  <i class="el-icon-position" style="opacity: 1" v-if="isbord"></i>
                                  <span>蜂鸟专送</span>
                              </div>
                          </div>
                      </section>
                      <section style="width: 100%">
                          <div class="sai_div4">
                              <div class="sai_div5">商家属性(可以多选)</div>
                              <ul class="filter_ul" >
                                  <li  @click="saixuan1">
                                  <!--<li>-->
                                      <i class="el-icon-check" v-if="isSai1"></i>
                                      <span class="filter_icon"  v-if="isbord1">品</span>
                                      <span class="sai_pin">品牌商家</span>
                                  </li>
                                  <li  @click="saixuan2">
                                  <!--<li>-->
                                      <i class="el-icon-check" v-if="isSai2"></i>
                                      <span class="filter_icon"  v-if="isbord2">保</span>
                                      <span class="sai_pin">外卖保</span>
                                  </li>
                                  <li  @click="saixuan3">
                                  <!--<li>-->
                                      <i class="el-icon-check" v-if="isSai3"></i>
                                      <span class="filter_icon"  v-if="isbord3">准</span>
                                      <span class="sai_pin">准时达</span>
                                  </li>
                                  <li  @click="saixuan4">
                                  <!--<li>-->
                                      <i class="el-icon-check" v-if="isSai4"></i>
                                      <span class="filter_icon"  v-if="isbord4">新</span>
                                      <span class="sai_pin">新店</span>
                                  </li>
                                  <li  @click="saixuan5">
                                  <!--<li>-->
                                      <i class="el-icon-check" v-if="isSai5"></i>
                                      <span class="filter_icon"  v-if="isbord5">付</span>
                                      <span class="sai_pin">在线支付</span>
                                  </li>
                                  <li  @click="saixuan6">
                                  <!--<li>-->
                                      <i class="el-icon-check" v-if="isSai6"></i>
                                      <span class="filter_icon"  v-if="isbord6">发</span>
                                      <span class="sai_pin">开发票</span>
                                  </li>
                              </ul>
                          </div>
                      </section>
                      <footer class="sai_footer">
                          <div class="footer_div1" @click="deleteBtn">情空</div>
                          <div class="footer_div2" @click="addBtn">
                              确定
                              <span style="color: #fff" v-if="shuo">({{number}})</span>
                          </div>
                      </footer>
                  </section>
              </div>
        </div>
        <!--赛选之前的-->
        <div class="shop_list_container">
            <div class="shoplist_container">
                <ul>
                    <li class="shop_li" v-for="item in shopPro" :key="item.id" @click="shopBtn(item)">
                        <section>
                            <img :src="'https://elm.cangdu.org/img/' + item.image_path" alt="" class="shop_img">
                        </section>
                        <div class="shop_right">
                            <div class="shop_detail_header">
                                <h4 class="shop_title ellipsis premium">
                                    <p class="pingpai">品牌</p>
                                    {{item.name}}
                                </h4>
                                <ul class="shop_detail_ul">
                                    <li class="supports">保</li>
                                    <li class="supports">准</li>
                                    <li class="supports">票</li>
                                </ul>
                            </div>
                            <h5 class="rating_order_num">
                                <section class="rating_order_num_left">
                                    <section class="rating_section">
                                        <div class="rating_container">
                                            <section class="star_container">

                                            </section>
                                        </div>
                                        <span class="rating_num">{{item.rating}}</span>
                                    </section>
                                    <section class="order_section">月售{{item.recent_order_num}}单</section>
                                </section>
                                <section class="rating_order_num_right">
                                    <span class="delivery_left">蜂鸟专送</span>
                                    <span class="delivery_right">准时达</span>
                                </section>
                            </h5>
                            <h5 class="fee_distance">
                                <p class="fee">
                                    ¥{{item.float_minimum_order_amount}}起送<span>/</span>配送费约¥{{item.float_delivery_fee}}
                                </p>
                                <p class="distance_time">
                                    <span>{{item.distance}}</span>
                                    <span>/</span>
                                    <span class="order_time">{{item.order_lead_time}}</span>
                                </p>
                            </h5>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Food",
        data(){
            return {
                shopPro:[],
                isShow:true,//分类显隐
                zuo_isShow:false,
                zuoisShow:false,

                isShow1:true,//排序显隐
                isPai:false,
                isPai1:false,

                isShow2:true,
                isShowup:false,//赛选显隐
                isShow3:false,


                fenName:'',//头部分类的名字
                fenArr:[],//所有商品分分类数组
                imgArr:[],//商品分类图标
                rightfenArr:[],//各自分类
                reqArr:[],//接收的数据
                rightID:'',//左边点击的id
                doneArr:[],//处理存储过的数据，然后v-for


                dian_pu:false,//点击赛选从新出现的店铺  把之前的隐藏
                paixu:false,//排序后的对号的影藏显示
                paixu1:false,
                paixu2:false,
                paixu3:false,
                paixu4:false,
                paixu5:false,

                //定义点击的++
                count:1,
                //确定后面的数
                number:0,
                //赛选对号的隐藏
                isSai:false,
                isbord:true,
                isSai1:false,
                isbord1:true,
                isSai2:false,
                isbord2:true,
                isSai3:false,
                isbord3:true,
                isSai4:false,
                isbord4:true,
                isSai5:false,
                isbord5:true,
                isSai6:false,
                isbord6:true,

                shuo:false,//数字

                user_active:[],//存商家活动列表
                activeArr:[],//存活动
                // cityLatitude:'',
                // cityLongitude:'',
                // cityAdress:'',
                // placeNameHistory:''
            }
        },
        methods:{

            //点击店铺跳转到详情
            shopBtn(item){
                console.log(item);
                localStorage.setItem("shopId",item.id);
                this.$router.push({path:"/shop"});
            },

            //点击左侧的分类展示右侧的分类
            dianfenBtn(i) {
                this.rightfenArr = i;
                console.log(i)
            },
            //点击分类的隐藏
            checkShow(){
               this.isShow = !this.isShow,
               this.zuo_isShow = !this.zuo_isShow,
               this.zuoisShow = !this.zuoisShow
            },
            //点击又边的小分类进行赛选
            fenlieclass(item1){
                this.axios.get('/shopping/restaurants?latitude=' + localStorage.getItem('latitude') + '&longitude=' + localStorage.getItem('longitude') + '&restaurant_category_ids[]=' + item1.id).then(res =>{
                    this.shopPro = res;
                    console.log(res);
                });
                this.zuoisShow = false;
            },
            //点击排序的隐藏
            paiBtn(){
                 this.isShow1 = !this.isShow1
                this.isPai = !this.isPai;
                 this.isPai1= !this.isPai1;
            },
            //点击智能排序
            chooseSort(){
                this.axios.get('/shopping/restaurants?latitude=' + localStorage.getItem('latitude') + '&longitude=' + localStorage.getItem('longitude') + '&order_by=4').then(res=>{
                    console.log(res);
                    this.shopPro = res;
                    this.dian_pu = true;
                }).catch(err=>{});
                this.isPai1 = false;
                // this.paixu = true;
            },
            //点击距离最近排序
            chooseSort1(){
                this.axios.get('/shopping/restaurants?latitude=' + localStorage.getItem('latitude') + '&longitude=' + localStorage.getItem('longitude') + '&order_by=5').then(res=>{
                    console.log(res);
                    this.shopPro = res;
                    this.dian_pu = true;
                }).catch(err=>{});
                this.isPai1 = false;
                // this.paixu1 = true;
            },
            //点击销量最高排序
            chooseSort2(){
                this.axios.get('/shopping/restaurants?latitude=' + localStorage.getItem('latitude') + '&longitude=' + localStorage.getItem('longitude') + '&order_by=6').then(res=>{
                    console.log(res);
                    this.shopPro = res;
                    this.dian_pu = true;
                }).catch(err=>{});
                this.isPai1 = false;
                // this.paixu2 = true;
            },
            //点击起送最低最高排序
            chooseSort3(){
                this.axios.get('/shopping/restaurants?latitude=' + localStorage.getItem('latitude') + '&longitude=' + localStorage.getItem('longitude') + '&order_by=1').then(res=>{
                    console.log(res);
                    this.shopPro = res;
                    this.dian_pu = true;
                }).catch(err=>{});
                this.isPai1 = false;
                // this.paixu3 = true;
            },
            //点击速度最快最高排序
            chooseSort4(){
                this.axios.get('/shopping/restaurants?latitude=' + localStorage.getItem('latitude') + '&longitude=' + localStorage.getItem('longitude') + '&order_by=2').then(res=>{
                    console.log(res);
                    this.doneArr = res;
                    this.dian_pu = true;
                }).catch(err=>{});
                this.isPai1 = false;
                // this.paixu4 = true;
            },
            //点击评分最高最高排序
            chooseSort5(){
                this.axios.get('/shopping/restaurants?latitude=' + localStorage.getItem('latitude') + '&longitude=' + localStorage.getItem('longitude') + '&order_by=3').then(res=>{
                    console.log(res);
                    this.shopPro = res;
                    this.dian_pu = true;
                    // this.paixu5 = true;
                }).catch(err=>{});
                this.isPai1 = false;

            },
            //点击上面的筛选按钮的显隐
            saiBtn(){
                   this.isShow2 = !this.isShow2;
                   this.isShowup = !this.isShowup;
                   this.isShow3 = !this.isShow3;
            },
            //赛选功能 蜂鸟的筛选
            saixuan(count){
                this.count++;
                this.isSai = !this.isSai;
                this.isbord= !this.isbord;
                if (count%2 == 0) {
                    this.number--;
                }else{
                    this.number++;
                }
                if (this.number == 0) {
                    this.shuo = false;
                }else{
                    this.shuo = true;
                }
                this.axios.get('/shopping/restaurants?latitude=' + this.$store.state.afterSearchLatitude + '&longitude=' + this.$store.state.afterSearchLongitude + '&delivery_mode[]=' + 1).then(res=>{
                    console.log(res)
                    this.shopPro = res;
                }).catch(err=>{});
                this.isShow2 = false;
            },
            saixuan1(){
                this.isSai1 = !this.isSai1;
                this.isbord1= !this.isbord1;
                if (this.isSai1){
                    this.activeArr.push(8);
                    this.number ++
                } else {
                    this.activeArr.splice(this.activeArr.indexOf(8),1)
                    this.number --;
                }
                console.log(this.activeArr);
                if (this.number == 0) {
                    this.shuo = false;
                }else{
                    this.shuo = true;
                }
            },
            saixuan2(){
                this.isSai2 = !this.isSai2;
                this.isbord2 = !this.isbord2;
                if (this.isSai2){
                    this.activeArr.push(7);
                    this.number ++;
                } else if (this.activeArr.length ===0) {
                }else {
                    this.activeArr.splice(this.activeArr.indexOf(7),1)
                    this.number --
                }
                console.log(this.activeArr);
                if (this.number == 0) {
                    this.shuo = false;
                }else{
                    this.shuo = true;
                }
            },
            saixuan3(){
                this.isSai3 = !this.isSai3;
                this.isbord3 = !this.isbord3;
                if (this.isSai3){
                    this.activeArr.push(9)
                    this.number ++;
                } else if (this.activeArr.length ===0) {
                }else {
                    this.activeArr.splice(this.activeArr.indexOf(9),1)
                    this.number --
                }
                console.log(this.activeArr);
                if (this.number == 0) {
                    this.shuo = false;
                }else{
                    this.shuo = true;
                }
            },
            saixuan4(){
                this.isSai4 = !this.isSai4;
                this.isbord4 = !this.isbord4;
                if (this.isSai4){
                    this.activeArr.push(5)
                    this.number ++;
                } else if (this.activeArr.length ===0) {
                }else {
                    this.activeArr.splice(this.activeArr.indexOf(5),1)
                    this.number --
                }
                console.log(this.activeArr);
                if (this.number == 0) {
                    this.shuo = false;
                }else{
                    this.shuo = true;
                }
            },
            saixuan5(){
                this.isSai5 = !this.isSai5;
                this.isbord5 = !this.isbord5;
                if (this.isSai5){
                    this.activeArr.push(3)
                    this.number ++
                } else if (this.activeArr.length ===0) {
                }else {
                    this.activeArr.splice(this.activeArr.indexOf(3),1)
                    this.number --
                }
                console.log(this.activeArr);
                if (this.number == 0) {
                    this.shuo = false;
                }else{
                    this.shuo = true;
                }
            },
            saixuan6(){
                this.isSai6 = !this.isSai6;
                this.isbord6 = !this.isbord6;
                if (this.isSai6){
                    this.activeArr.push(4)
                    this.number ++
                } else if (this.activeArr.length ===0) {
                }else {
                    this.activeArr.splice(this.activeArr.indexOf(4),1)
                        this.number --
                }
                console.log(this.activeArr);
                if (this.number == 0) {
                    this.shuo = false;
                }else{
                    this.shuo = true;
                }
            },
            //点击清空清空
            deleteBtn(){
                activeArr:[];
                  this.number = 0;
                  this.shuo = false,
                    this.isSai = false,
                    this.isbord = true,
                    this.isSai1 = false,
                    this.isbord1 = true,
                    this.isSai2 = false,
                    this.isbord2 = true,
                    this.isSai3 = false,
                    this.isbord3 = true,
                    this.isSai4 = false,
                    this.isbord4 = true,
                    this.isSai5 = false,
                    this.isbord5 = true,
                    this.isSai6 = false,
                    this.isbord6 = true

            },
            //点击确定进行
            addBtn(){
                this.axios.get('https://elm.cangdu.org/shopping/restaurants?latitude=' + this.$store.state.afterSearchLatitude + '&longitude=' + this.$store.state.afterSearchLongitude + '&support_ids[]=' + this.activeArr).then(res=>{
                    console.log(res)
                    this.shopPro = res
                }).catch(err=>{});
                this.isShow3 = false;
            }

        },
        created(){
            this.fenName = localStorage.getItem("foodTitle");

            this.axios.get('/shopping/restaurants?latitude=' + localStorage.getItem('latitude') + '&longitude=' + localStorage.getItem('longitude') + '').then((res) => {
                this.shopPro = res;
                console.log(res)
            });
        },
        mounted(){
            //商家活动的请求
            this.axios.get("/shopping/v1/restaurants/activity_attributes").then(res=>{
                console.log(res);
                user_active = res;
            }).catch(err=>{});

            this.axios.get("/shopping/v2/restaurant/category").then(res=>{
                console.log(res);
                this.fenArr = res;
                this.rightfenArr = res[0].sub_categories;
                this.rightID = res.id;
                //处理图片格式
                for (let s of this.fenArr) {
                    let len = s.image_url.length;
                    if (s.image_url.slice(len - 4) === 'jpeg') {
                        this.srcS = 'https://fuss10.elemecdn.com/' + s.image_url.slice(0, 1) + '/' + s.image_url.slice(1, 3) + '/' + s.image_url.slice(3) + '.jpeg';
                    } else if (s.image_url.slice(len - 3) === 'png') {
                        this.srcS = 'https://fuss10.elemecdn.com/' + s.image_url.slice(0, 1) + '/' + s.image_url.slice(1, 3) + '/' + s.image_url.slice(3) + '.png';
                    } else {
                        this.srcS = 'https://elm.cangdu.org/img/default.jpg'
                    }
                    this.imgArr.push(this.srcS);
                }
            }).catch(err=>{})
        }
    }
</script>

<style scoped>
    .foodWarp{
        padding-top: 1.95rem;
        /*background-color: azure;*/
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>a{
        position: absolute;
        top: 50%;
